
import Vue from "vue";

export default Vue.extend({
  name: "Home",
  data() {
    return {
      windowWidth: window.innerWidth
    }
  },

  computed: {
    isMobile(): boolean {
      return this.windowWidth <= 720
    }
  },
  mounted() {
    window.onresize = () => {
      this.windowWidth = window.innerWidth;
    }
  },
})
