
import Vue from "vue";

export default Vue.extend({
  name: "Info",
  data() {
    return {
      brackets: [['Gold',3,3,5], ['Platinum',3,5,7], ['Diamond',5,7,9]],
      colors: {'Gold': 'yellow'},
      windowWidth: window.innerWidth
    }
  },
  computed: {
    isMobile(): boolean {
      return this.windowWidth <= 720
    }
  },
  mounted() {
    window.onresize = () => {
      this.windowWidth = window.innerWidth;
    }
  },
})
